import React  from 'react';

import swal from 'sweetalert';
import FormPic from '../assets/images/form-pic.png'
import {useForm} from 'react-hook-form' ;




  const ValidationForm = () => {
   const {register, handleSubmit, formState:{errors}} = useForm();
   
   console.log(errors)
   
  return (
    <>
    <div className='form-section'>
        <div className='form-section-left'>
          <div className='form-header'>
            <h1>заказать Консультацию специалиста </h1>
            <span>Заполните форму и мы свяжемся с вами в ближайшее время!</span>
          </div>
        
        <div className='form-page'>
           <form onSubmit={handleSubmit (() => {  
            
              swal("Благодарю вас!", "Ваш запрос зарегистрирован.", "success")
    })}
           >
           <div className='name-phone-inputs'>
             <div className='user-name-input'>
              <input  placeholder='Ваше имя' type='text'
                {...register("userName", {
                required: true,
                pattern: /^[A-Za-z]+$/i
                })}

              /> 
              {errors?.userName?.type === "required" && <span>Это поле обязательно к заполнению</span>}
              {errors?.userName?.type === "pattern" && (<span>Только буквенные символы</span>)}
             </div>
             
             <div className='phone-number-input'>
              <input   placeholder='Номер вашего телефона' type="text" 
                {...register("phoneNumber", {
                required: true,
                pattern: /^\+?([0-9]{3})\)?[]?([0-9]{3})[]?([0-9]{2})[]?([0-9]{2})$/
                })}
              />
              {errors?.phoneNumber?.type === "required" && <span>Это поле обязательно к заполнению</span>}
              {errors?.phoneNumber?.type === "pattern" && (<span>Только цифровые символы, например: +994-xxx-xx-xx</span>)}
             </div>
             
           </div>
           
           <div className='question-area'>
            <textarea type='text' placeholder='Ваш вопрос'
            {...register("question", {
                required: true,
                })}
            >
            
            </textarea>
            {errors?.question?.type === "required" && <span>Это поле обязательно к заполнению</span>}
           </div>
           
           <div className='check-boxes'>
           <div className='checked'>
           <label className='check-box'>
               <input type="checkbox"
                {...register("checkbox",{
                  required: true
                })}
               />
               <span className='checkmark' ></span>
             </label>  
              <p>Я согласен с условиями обработки персональных данных </p>
              </div>  
             
            {/* <div className='error-checkbox'> */}
            {errors?.checkbox?.type === "required" && <span>вы должны согласиться с условиями конфиденциальности</span>} 
            {/* </div>  */}
           </div>
            
           <div className='submit-btn'>
            <button>Отправить</button>
           </div>
           
           </form>  
        </div>
        
        </div>

        <div className='form-section-right'>
            <img src={FormPic}/>
        </div>
    </div>
    </>
  )
  }

export default ValidationForm