import React from 'react';

import IntroPic from '../assets/images/intro-pic.png'

const Intro = () => {
  return (
    <>
    <div className='intro-section'>
        <div className='intro-left-side'>
           <div className='header'>
            <p className='top'>Студия дизайна</p>
            <p className='bottom'>полного цикла</p> 
             <span>От дизайна визиток до дизайна сайтов</span>
           </div>

           <div className='header-buttons'>
            <button>Комплексные решения</button>
            <button>Логотипы</button>
            <button>Дизайн</button>
           </div>

        </div>

       <div className='intro-right-side'>
        <img className='intro-pic' src={IntroPic}/>
       </div> 

    </div>
    </>
  )
}

export default Intro