import React from 'react';
import { useState } from 'react';

import LogoPic from '../assets/images/logo-pic.png';
import MenuIcon from '../assets/svg/menu-icon.svg';

const Navbar = () => {
  
    const [isNavExpanded, setIsNavExpanded] = useState(false);

    const openMenu = () => {
        setIsNavExpanded(!isNavExpanded)
    }
  
    return (
    <>
        <div className='nav-bar'>
            <div className='logo-section'>
                <img className='logo' src={LogoPic}></img>
            </div>
            
            <button className='menu-icon' onClick={openMenu} >               
               <img src={MenuIcon}/>
            </button>
            
            
            <div className={ isNavExpanded ? "nav-links-expanded" : "nav-links"}>
                <a href='#'>Главная</a>
                <a href='#'>Услуги </a>
                <a href='#'>Наши работы </a>
                <a href='#'>Контакты</a>
            </div>

            <div className='button-section'>
                <button className='btn'>Заказать звонок</button>
            </div> 

        </div>
    </>
  )
}

export default Navbar